<template>
  <div class="number-segment-generator">
    <el-card class="input-card" shadow="hover">
      <h2>üî¢ ÊâãÊú∫Âè∑Á†ÅÊÆµÁîüÊàêÂô® (Âä®ÊÄÅ‰ΩçÊï∞)</h2>
      <p class="desc">
        Ê†πÊçÆÊÇ®ÊåáÂÆöÁöÑÂè∑Á†ÅÂâçÁºÄ„ÄÅ‰∏≠Èó¥ËåÉÂõ¥ÂíåÂêéÁºÄÔºåÊâπÈáèÁîüÊàêÂè∑Á†ÅÂàóË°®„ÄÇ**ÊÇ®ÂèØ‰ª•Ëá™ÂÆö‰πâ‰∏≠Èó¥Âè∑Á†ÅÁöÑ‰ΩçÊï∞**„ÄÇ
      </p>

      <div class="input-area">
        <el-form :inline="true" label-position="top">

          <el-form-item label="ÂâçÁºÄ (Prefix)">
            <el-input
                v-model="prefix"
                placeholder="Â¶Ç 138"
                class="prefix-suffix-input"
                maxlength="11"
            ></el-input>
          </el-form-item>

          <el-form-item label="‰∏≠Èó¥Âè∑Á†Å‰ΩçÊï∞ (N)">
            <el-input-number
                v-model.number="middleDigits"
                :min="1"
                :max="7"
                controls-position="right"
                size="small"
                class="digits-input"
                @change="handleDigitsChange"
            ></el-input-number>
            <el-tooltip content="ÊúÄÂ§ßÈôêÂà∂ 7 ‰Ωç (1000‰∏áÊù°)ÔºåÈÅøÂÖçÊµèËßàÂô®Âç°È°ø" placement="top">
              <i class="el-icon-warning-outline info-icon"></i>
            </el-tooltip>
          </el-form-item>

          <el-form-item :label="'Âè∑Á†ÅËåÉÂõ¥ (' + middleDigits + '‰Ωç)'">
            <div class="middle-range">
              <el-input
                  v-model="startRange"
                  type="number"
                  :placeholder="'Ëµ∑Âßã (Â¶Ç ' + startPlaceholder + ')'"
                  :maxlength="middleDigits"
                  class="range-input"
                  @change="handleRangeChange('start')"
              ></el-input>
              <span class="range-separator"> - </span>
              <el-input
                  v-model="endRange"
                  type="number"
                  :placeholder="'ÁªìÊùü (Â¶Ç ' + endPlaceholder + ')'"
                  :maxlength="middleDigits"
                  @change="handleRangeChange('end')"
                  class="range-input"
              ></el-input>
            </div>
          </el-form-item>

          <el-form-item label="ÂêéÁºÄ (Suffix)">
            <el-input
                v-model="suffix"
                placeholder="Â¶Ç 888"
                class="prefix-suffix-input"
                maxlength="11"
            ></el-input>
          </el-form-item>
        </el-form>

        <div class="control-row">
          <el-button
              type="primary"
              @click="generateNumbers"
              :loading="loading"
              class="generate-button"
          >
            {{ loading ? 'ÁîüÊàê‰∏≠...' : 'ÂºÄÂßãÁîüÊàê (' + totalCount.toLocaleString() + ' Êù°)' }}
          </el-button>

          <el-button
              type="success"
              @click="downloadResult('txt')"
              :disabled="!generatedData.length"
              class="download-button"
          >
            ‰∏ãËΩΩ TXT
          </el-button>

          <el-button
              type="warning"
              @click="downloadResult('csv')"
              :disabled="!generatedData.length"
              class="download-button"
          >
            ‰∏ãËΩΩ CSV
          </el-button>
        </div>
      </div>
    </el-card>

    <el-card v-if="generatedData.length" class="result-card" shadow="hover">
      <h3>ÁîüÊàêÁªìÊûú (ÊÄªÊï∞: {{ generatedData.length.toLocaleString() }})</h3>

      <el-input
          type="textarea"
          :rows="15"
          :value="displayData"
          readonly
          placeholder="ÁîüÊàêÁöÑÂè∑Á†ÅÂàóË°®Â∞ÜÊòæÁ§∫Âú®ËøôÈáå..."
      ></el-input>
    </el-card>
  </div>
</template>

<script>
import { defineComponent } from 'vue';

export default defineComponent({
  name: "NumberSegmentGenerator",
  metaInfo: {
    title: 'ÊâãÊú∫Âè∑Á†ÅÊÆµÁîüÊàêÂô® - ÊâπÈáèÁîüÊàêÊµãËØïÊï∞ÊçÆ‰∏éÂè∑Á†ÅË¶ÜÁõñÂàÜÊûêÂ∑•ÂÖ∑',
    meta: [
      {
        name: 'description',
        content: '‰∏ì‰∏öÁöÑÁ∫ØÂâçÁ´ØÊâãÊú∫Âè∑Á†ÅÊÆµÁîüÊàêÂ∑•ÂÖ∑„ÄÇËá™ÂÆö‰πâÂè∑Á†ÅÊÆµÁöÑ‰ΩçÊï∞ÔºàNÔºâÔºåÁîüÊàê 10^N ‰∏™Âè∑Á†ÅÂàóË°®ÔºåËá™Âä®Ë°•Èõ∂ÔºåÁî®‰∫éÊï∞ÊçÆÊµãËØï„ÄÅË¶ÜÁõñÂàÜÊûêÂíåÊâπÈáèÊï∞ÊçÆÂ°´ÂÖÖ„ÄÇ'
      },
      {
        name: 'keywords',
        content: 'Âè∑Á†ÅÊÆµÁîüÊàêÂô®,ÊâãÊú∫Âè∑ÁîüÊàê,ÊâπÈáèÁîüÊàêÊï∞ÊçÆ,Âä®ÊÄÅ‰ΩçÊï∞ÁîüÊàê,Ëá™Âä®Ë°•Èõ∂,ÊµãËØïÊï∞ÊçÆ,Á∫ØÂâçÁ´ØÂ∑•ÂÖ∑'
      }
    ]
  },
  data() {
    return {
      prefix: '',
      suffix: '',
      middleDigits: 5,
      startRange: '0',
      endRange: '99999',
      loading: false,
      generatedData: [],
      displayLimit: 5000,
      startPlaceholder: '0',
      endPlaceholder: '99999',
    };
  },
  computed: {
    displayData() {
      if (!this.generatedData.length) return '';
      const display = this.generatedData.slice(0, this.displayLimit).join('\n');
      if (this.generatedData.length > this.displayLimit) {
        return display + `\n... (‰ªÖÊòæÁ§∫Ââç ${this.displayLimit.toLocaleString()} Êù°ÔºåËØ∑ÁÇπÂáª‰∏ãËΩΩÊåâÈíÆËé∑ÂèñÂÖ®ÈÉ®Êï∞ÊçÆ)`;
      }
      return display;
    },
    totalCount() {
      const startNum = parseInt(this.startRange, 10);
      const endNum = parseInt(this.endRange, 10);

      if (isNaN(startNum) || isNaN(endNum) || endNum < startNum) return 0;

      const maxPossibleValue = Math.pow(10, this.middleDigits) - 1;

      const effectiveEnd = Math.min(endNum, maxPossibleValue);
      const effectiveStart = startNum;

      if (effectiveEnd < effectiveStart) return 0;

      return effectiveEnd - effectiveStart + 1;
    }
  },
  watch: {
    // ÁõëÂê¨‰ΩçÊï∞ÂèòÂåñÔºå‰ΩÜÁõ¥Êé•Ë∞ÉÁî®‰∏ìÈó®Â§ÑÁêÜ‰ΩçÊï∞ÁöÑ‰∫ã‰ª∂
    // middleDigits: 'handleDigitsChange', // ÁßªÂà∞ @change
  },
  methods: {
    // ‚ö†Ô∏è ‰øÆÊ≠£ÂêéÁöÑÊñπÊ≥ïÔºöÂ§ÑÁêÜËåÉÂõ¥ËæìÂÖ•ÂÄºÁöÑÁü´Ê≠£
    handleRangeChange(type) {
      const length = this.middleDigits;
      const maxPossibleValue = Math.pow(10, length) - 1;
      let inputValue = type === 'start' ? this.startRange : this.endRange;

      let value = parseFloat(inputValue);

      // 1. Ê†°È™åÔºöÈùûÊï∞Â≠óÊàñË¥üÊï∞ -> Áü´Ê≠£‰∏∫ 0
      if (isNaN(value) || value < 0) {
        value = 0;
        this.$message.warning('ËæìÂÖ•ÂÄº‰∏çÂêàÊ≥ïÊàñ‰∏∫Ë¥üÊï∞ÔºåÂ∑≤Ëá™Âä®Áü´Ê≠£‰∏∫ 0„ÄÇ');
      }

      // 2. Ê†°È™åÔºöÂ∏¶Â∞èÊï∞ÁÇπ -> Âêë‰∏ãÂèñÊï¥
      if (value !== Math.floor(value)) {
        value = Math.floor(value);
        this.$message.warning('ËæìÂÖ•ÂÄºÂåÖÂê´Â∞èÊï∞ÔºåÂ∑≤Ëá™Âä®ÂèñÊï¥„ÄÇ');
      }

      // 3. Ê†°È™åÔºöË∂ÖÂá∫ÊúÄÂ§ßËåÉÂõ¥ -> Áü´Ê≠£‰∏∫ÊúÄÂ§ßÂÄº
      if (value > maxPossibleValue) {
        value = maxPossibleValue;
        this.$message.warning(`ËæìÂÖ•ÂÄºÂ∑≤Ë∂ÖÂá∫ ${length} ‰ΩçÁöÑÊúÄÂ§ßËåÉÂõ¥ (${maxPossibleValue})ÔºåÂ∑≤Ëá™Âä®Ë∞ÉÊï¥„ÄÇ`);
      }

      const correctedValue = String(value);

      // 4. Êõ¥Êñ∞ÁªëÂÆöÁöÑÊï∞ÊçÆ
      if (type === 'start') {
        this.startRange = correctedValue;
      } else if (type === 'end') {
        const startNum = parseInt(this.startRange, 10);
        if (value < startNum) {
          // Â¶ÇÊûúÁü´Ê≠£ÂêéÁöÑÁªìÊùüÂÄºÂ∞è‰∫éËµ∑ÂßãÂÄºÔºåÊèêÈÜíÁî®Êà∑Ôºå‰ΩÜ‰∏çÂº∫Âà∂ËÆæÁΩÆÔºàtotalCount ‰ºöÈòªÊ≠¢ÁîüÊàêÔºâ
          this.$message.warning('ÁªìÊùüÂÄºÂ∞è‰∫éËµ∑ÂßãÂÄºÔºåÂ∞ÜÊó†Ê≥ïÁîüÊàêÂè∑Á†Å„ÄÇ');
        }
        this.endRange = correctedValue;
      }
    },

    // ‚ö†Ô∏è ‰øÆÊ≠£ÂêéÁöÑÊñπÊ≥ïÔºöÂ§ÑÁêÜ‰ΩçÊï∞ÂèòÂåñÊó∂ÔºåÈáçÁΩÆËåÉÂõ¥Âíå Placeholder
    handleDigitsChange() {
      const length = this.middleDigits;

      // 1. Êõ¥Êñ∞ placeholder ÊèêÁ§∫
      this.startPlaceholder = '0';
      this.endPlaceholder = '9'.repeat(length);

      // 2. ÈáçÁΩÆÂÆûÈôÖÂÄºÔºàËøôÊòØÊúÄÂÆâÂÖ®ÁöÑÂÅöÊ≥ïÔºåÈÅøÂÖçÊóßÂÄºÂíåÊñ∞‰ΩçÊï∞ÈïøÂ∫¶ÂÜ≤Á™ÅÔºâ
      this.startRange = this.startPlaceholder;
      this.endRange = this.endPlaceholder;

      // 3. ËøêË°å‰∏ÄÊ¨°Áü´Ê≠£Ôºå‰ª•Á°Æ‰øù newVlaue ÊòØÂêàÊ≥ïÁöÑ (ËôΩÁÑ∂ input-number Â∑≤ÁªèÈôêÂà∂‰∫Ü min/max)
      this.handleRangeChange('start');
      this.handleRangeChange('end');
    },

    // ‚ö†Ô∏è Ê†∏ÂøÉÁîüÊàêÈÄªËæëÔºåÂú®ÁîüÊàêÂâçËøõË°åÊúÄÁªàÁü´Ê≠£
    generateNumbers() {
      // Á°Æ‰øùÂú®ÁîüÊàêÂâçÂÖàÊâßË°å‰∏ÄÊ¨°Áü´Ê≠£ÔºåÈò≤Ê≠¢Áî®Êà∑Âú®ËæìÂÖ•ÂêéÊú™Â§±ÁÑ¶Áõ¥Êé•ÁÇπÂáªÁîüÊàê
      this.handleRangeChange('start');
      this.handleRangeChange('end');

      const startNum = parseInt(this.startRange, 10);
      const endNum = parseInt(this.endRange, 10);

      // È¢ÑÊ£ÄÊü•
      if (isNaN(startNum) || isNaN(endNum) || this.totalCount <= 0) {
        this.$message.warning('ËåÉÂõ¥ËÆæÁΩÆÊúâËØØÔºåËØ∑Á°Æ‰øùËµ∑ÂßãÂ∞è‰∫éÁ≠â‰∫éÁªìÊùüÔºå‰∏îËæìÂÖ•ÂêàÊ≥ï„ÄÇ');
        return;
      }

      // ÊÄßËÉΩÊ£ÄÊü•
      if (this.totalCount > Math.pow(10, 7) + 1) {
        this.$message.error('ÁîüÊàêÊï∞ÈáèËøáÂ§ßÔºåËØ∑Â∞Ü‰ΩçÊï∞ÈôêÂà∂Âú® 7 ‰Ωç‰ª•ÂÜÖÊàñÁº©Â∞èËåÉÂõ¥„ÄÇ');
        return;
      }

      this.loading = true;
      this.generatedData = [];
      const length = this.middleDigits;

      // ‰ΩøÁî®Áü´Ê≠£ÂêéÁöÑÂÄº
      const finalStart = startNum;
      const finalEnd = endNum;

      setTimeout(() => {
        const results = [];
        const prefixStr = this.prefix || '';
        const suffixStr = this.suffix || '';

        for (let i = finalStart; i <= finalEnd; i++) {
          const middle = String(i).padStart(length, '0');
          results.push(prefixStr + middle + suffixStr);
        }

        this.generatedData = results;
        this.loading = false;
        this.$message.success(`ÊàêÂäüÁîüÊàê ${results.length.toLocaleString()} Êù°Âè∑Á†Å„ÄÇ`);
      }, 50);

    },

    downloadResult(format) {
      if (!this.generatedData.length) {
        this.$message.warning('ËØ∑ÂÖàÁîüÊàêÂè∑Á†ÅÂàóË°®„ÄÇ');
        return;
      }

      let content = '';
      let mimeType = '';

      if (format === 'csv') {
        const csvHeader = 'Phone Number\n';
        content = csvHeader + this.generatedData.join('\n');
        mimeType = 'text/csv;charset=utf-8';
      } else { // txt Ê†ºÂºè
        content = this.generatedData.join('\n');
        mimeType = 'text/plain;charset=utf-8';
      }

      const blob = new Blob([content], { type: mimeType });
      const filename = `numbers_${Date.now()}.${format}`;

      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = filename;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      window.URL.revokeObjectURL(url);
    }
  },
  created() {
    this.handleDigitsChange();
  }
});
</script>

<style scoped>
/* Ê†∑Âºè‰∏é‰∏ä‰∏Ä‰∏™ÁâàÊú¨‰øùÊåÅ‰∏ÄËá¥ */
.number-segment-generator {
  max-width: 900px;
  margin: 20px auto;
  padding: 0 10px;
}
.input-card {
  margin-bottom: 20px;
}
h2 {
  font-size: 24px;
}
.desc {
  color: #909399;
  font-size: 14px;
  margin-bottom: 20px;
}

/* Ê†∏ÂøÉËæìÂÖ•Âå∫ÂüüÁöÑÂ∏ÉÂ±Ä */
.input-area .el-form-item {
  margin-right: 20px;
  margin-bottom: 20px;
  display: inline-block;
  vertical-align: top;
}

.prefix-suffix-input {
  width: 150px;
}

.middle-range {
  display: flex;
  align-items: center;
  width: 250px;
}

.range-input {
  flex: 1;
}

.range-separator {
  margin: 0 10px;
  color: #606266;
  font-weight: bold;
}

.digits-input {
  width: 150px;
}

.info-icon {
  margin-left: 5px;
  color: #E6A23C;
  cursor: pointer;
}

.control-row {
  margin-top: 20px;
  display: flex;
  gap: 10px;
}

.generate-button {
  flex-grow: 1;
}

.download-button {
  flex-grow: 0.5;
}

.result-card {
  margin-top: 20px;
}

/* ÊâãÊú∫Á´ØÊéíÁâà‰ºòÂåñ */
@media (max-width: 768px) {
  .number-segment-generator {
    padding: 0 5px;
  }

  /* Âº∫Âà∂ÊâÄÊúâËæìÂÖ•È°πÂûÇÁõ¥ÊéíÂàó */
  .input-area .el-form-item {
    display: block;
    margin-right: 0;
    width: 100%;
  }

  /* Ë∞ÉÊï¥ËæìÂÖ•Ê°ÜÂÆΩÂ∫¶‰ª•ÈÄÇÂ∫îÂÖ®ÂÆΩ */
  .prefix-suffix-input,
  .middle-range,
  .digits-input {
    width: 100%;
  }

  .control-row {
    flex-direction: column;
  }

  .generate-button,
  .download-button {
    width: 100%;
    flex-grow: 1;
    margin-left: 0 !important;
  }
}
</style>